  //?Expense Reducer 
// Aqui ficaram todas as manupulaes que ocorrero com o modelo expenses
const expensesReducerDefaltState = [];

export default (state=expensesReducerDefaltState, action) => {
    switch (action.type) {
        case 'ADD_EXPENSE':
            return [...state, action.expense];

        case 'REMOVE_EXPENSE':
            return state.filter(({id}) => id !== action.id);

        case 'EDIT_EXPENSE':
            return state.map((expense) => {
                if (expense.id === action.id){
                    return{
                        ...expense,
                        ...action.updates
                    }
                }else {
                    return expense;
                }
            });

        default:
            return state;
    }
};

